import Popup from './popup.vue'
export default {
  name: "",
  components: {
    Popup
  },
  data() {
    return {
      rotateChance: 2, // 可旋转次数
      rotateBtnDisabled: false, // 旋转按钮是否无法点击
      hasPrize: false, // 是否中奖
      styleTransform: "",
      styleTransition: "transform 7s ease-in-out",
      rotateStartAngle: 0,
      resultList: [
        {
          isPrize: true,
          isRedBag: true,
          amount: 3.88,
          url: require("@/assets/images/lottery/qrcode.png")
        },
        {
          name: "谢谢参与",
          isPrize: false,
        },
        {
          isPrize: true,
          isRedBag: false,
          url: require("@/assets/images/lottery/prize-qiyi.png")
        },
        {
          name: "谢谢参与",
          isPrize: false
        },
        {
          isPrize: true,
          isRedBag: true,
          amount: 8.88,
          url: require("@/assets/images/lottery/qrcode.png")
        },
        {
          name: "谢谢参与",
          isPrize: false
        },
        {
          isPrize: true,
          isRedBag: false,
          url: require("@/assets/images/lottery/prize-qiyi.png")
        },
        {
          name: "谢谢参与",
          isPrize: false
        }
      ],
      resultIndex: null, // 转轮结果下标
      resultItem: {}, // 转轮结果对象
      isPopupShow: false
    };
  },
  props: [""],
  methods: {
    handeClick() {
      if (this.rotateBtnDisabled) return;
      if (this.rotateChance > 0) {
        this.rotateChance--;
        this.rotateBtnDisabled = true;
      } else {
        return;
      }
      this.getResult(); // 计算抽奖结果
      this.beginRotate(); // 根据结果旋转转盘
    },
    getResult() {
      this.resultIndex = 0;
    },
    beginRotate() {
      let fullCircleCount = 10;
      let rotateAngle =
        22.5+
        this.rotateStartAngle +
        fullCircleCount * 360 +
        45 * this.resultIndex -
        this.rotateStartAngle % 360;
      this.styleTransform = "rotate(" + rotateAngle + "deg)";
      this.rotateStartAngle = rotateAngle; // 设置下次旋转的起始度数
      this.resultItem = this.resultList[this.resultIndex];
      this.endRotate();
    },
    endRotate() {
      let _this = this;
      setTimeout(() => {
        _this.rotateBtnDisabled = false;
        _this.isPopupShow = true;
        let url = _this.resultItem.isPrize
          ? "/lottery/prize"
          : "/lottery/thanks";
      }, 7000 + 500);
    }
  }
};



